
.search-condition-item-wrap {
  height: 3em;
  line-height: 3em;
  margin: 1em 0px;
  font-size: 16px;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-title-wrap {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 0px 1em;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-title-wrap .all-item-search {
      width: 3em;
      text-align: center;
      cursor: pointer;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-item-content {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 1em;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-item-content .search-item-name {
      padding: 0px 1em;
      cursor: pointer;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-item-content .more {
      cursor: pointer;
      opacity: 0.6;
}
.search-condition-item-wrap .search-content-list .search-content-item .search-item-content .multiple-select {
      border: 1px solid #ccc;
      padding: 0px 1em;
      height: 2em;
      line-height: 2em;
      border-radius: 5px;
      cursor: pointer;
}
.search-condition-item-wrap .search-content-list .search-content-item .selected-item {
    background: rgba(34, 182, 255, 0.95);
    color: wheat;
    border-radius: 5px;
}



