
.new-invest-news-warp {
  width: 200px;
  margin-bottom: .5em;
  cursor: pointer;
  margin: 1em auto;
  border: 1px solid #ccc;
  padding: .5em;
  border-radius: 5px;
}
.new-invest-news-warp .invest-img-wrap {
    width: 180px;
}
.new-invest-news-warp .invest-img-wrap img {
      width: 100%;
}
.new-invest-news-warp .invest-title {
    font-size: 16px;
    margin: .1em 0px .5em 0px;
}
.new-invest-news-warp .invest-info .info-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}
.new-invest-news-warp .invest-info .info-item .item-name {
      margin-right: 1em;
}



