
.policy-support-item-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
}
.policy-support-item-wrap .left-content {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 1em;
    background: white;
    margin-right: 1em;
}
.policy-support-item-wrap .left-content .item-main-wrap .img-wrap {
      width: 180px;
}
.policy-support-item-wrap .left-content .item-main-wrap .img-wrap img {
        width: 100%;
}
.policy-support-item-wrap .left-content .item-main-wrap .text {
      font-size: 22px;
      color: black;
      text-align: center;
      height: 4em;
      line-height: 3em;
}
.policy-support-item-wrap .left-content .item-content-wrap .item-content .item-img-wrap {
      width: 215px;
}
.policy-support-item-wrap .left-content .item-content-wrap .item-content .item-img-wrap img {
        width: 100%;
}
.policy-support-item-wrap .right-item-wrap {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 1em;
    background: white;
}
.policy-support-item-wrap .right-item-wrap .item-content-wrap {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: start;
          justify-content: flex-start;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      width: 450px;
}
.policy-support-item-wrap .right-item-wrap .item-content-wrap .item-content {
        cursor: pointer;
        position: relative;
        padding: 0px .5em .5em 0px;
}
.policy-support-item-wrap .right-item-wrap .item-content-wrap .item-content .text {
          position: absolute;
          font-size: 18px;
          left: 50%;
          top: 50%;
          color: white;
          transform: translate(-50%, -50%);
}
.policy-support-item-wrap .right-item-wrap .item-content-wrap .item-content .item-img-wrap {
          width: 215px;
          height: 105px;
          padding: .6em;
}
.policy-support-item-wrap .right-item-wrap .item-content-wrap .item-content .item-img-wrap img {
            width: 100%;
}



