
.survy-wrap {
  padding: 2em 3em;
  background: white;
  margin-top: 1em;
}
.survy-wrap .survy-title {
    text-align: center;
    font-size: 20px;
    line-height: 3em;
}
.survy-wrap .survy-intrduce-content {
    font-size: 16px;
}
.survy-wrap .survy-intrduce-content .survy-intrduce {
      text-indent: 2em;
      line-height: 2em;
}
.survy-wrap .survy-intrduce-content .survy-last {
      text-align: right;
}
.survy-wrap .survy-intrduce-list .survy-item-wrap {
    padding: 1em;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin: 1em 0px;
}
.survy-wrap .survy-intrduce-list .survy-item-wrap .icons {
      font-size: 12px;
      transform: scale(0.7);
      color: #ccc;
      display: inline-block;
      padding-right: 1em;
}
.survy-wrap .survy-intrduce-list .survy-item-wrap .survy-answer-list {
      margin-left: 2em;
}



