
.register-page-warp {
  width: 390px;
  margin: 1em auto;
  padding: 2em 3em;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: white;
}
.register-page-warp .login-img {
    width: 300px;
    text-align: center;
}
.register-page-warp .login-img img {
      width: 60%;
}
.register-page-warp .input-item {
    padding: 1em 0px;
}
.register-page-warp .phone-wrap {
    padding: 0.5em 0px;
}
.register-page-warp .phone-wrap .phone-verification-wrap {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      -ms-flex-align: center;
          align-items: center;
}
.register-page-warp .passWord-wrap {
    padding: 1em 0px;
}
.register-page-warp .btn-wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-item-align: center;
        align-self: center;
    width: 60%;
    margin: 0px auto;
}
.register-page-warp .text {
    font-size: 16px;
}
.register-page-warp .has-accout {
    text-align: center;
}
.register-page-warp .has-accout .sign-in {
      cursor: pointer;
      color: #2D8CF0;
}



