
.server-wrap {
  margin: 1.3em auto;
}
.server-wrap .carousel-wrap img {
    width: 100%;
}
.server-wrap .search-wrap {
    margin: 1em 0px;
    padding: 2em;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: white;
}
.server-wrap .search-wrap .title {
      font-size: 18px;
      display: inline-block;
      margin-right: 1em;
}
.server-wrap .search-result {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.server-wrap .search-result .left-wrap .item-container {
      margin: 1em 0px;
      display: -ms-flexbox;
      display: flex;
}
.server-wrap .search-result .left-wrap .solt-container {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.server-wrap .search-result .left-wrap .solt-container .soft-items-wrap .soft-items {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.server-wrap .search-result .left-wrap .solt-container .soft-items-wrap .more-soft {
        background: white;
