}
.server-wrap .search-result .left-wrap .fund-container .left-fund-item .applaction-soft-items {
        margin: 0.5em 0px;
}
.server-wrap .search-result .left-wrap .item-title {
      border-radius: 8px;
      background-color: #ff3300;
      color: white;
      padding: 1em 2em 7em 1em;
      font-size: 18px;
      width: 137px;
}



