
.footer-warp {
  margin-bottom: 3em;
  background-color: gray;
  color: white;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  padding: 1em;
}
.footer-warp .foot-img-wrap {
    width: 200px;
}
.footer-warp .foot-img-wrap img {
      width: 100%;
}
.footer-warp .foot-content {
    display: -ms-flexbox;
    display: flex;
    margin-left: 2%;
    -ms-flex-item-align: start;
        align-self: flex-start;
    width: 100%;
}
.footer-warp .foot-content .foot-item {
      width: 25%;
}
.footer-warp .foot-content .foot-item .item-title {
        font-size: 16px;
}
.footer-warp .foot-content .foot-item li {
        list-style: none;
        line-height: 1.5em;
        height: 1.5em;
        cursor: pointer;
}



