
.login-page-warp {
  width: 390px;
  margin: 1em auto;
  padding: 2em 3em;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: white;
}
.login-page-warp .login-img {
    width: 300px;
    text-align: center;
}
.login-page-warp .login-img img {
      width: 60%;
}
.login-page-warp .name-warp {
    padding: 1em 0px;
}
.login-page-warp .passWord-wrap {
    padding: 1em 0px;
}
.login-page-warp .btn-wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-item-align: center;
        align-self: center;
    width: 60%;
    margin: 0px auto;
}
.login-page-warp .text {
    font-size: 16px;
}



