
.company-intro-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 2em;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 1em 0px;
}
.company-intro-wrap .left-info-wrap {
    display: -ms-flexbox;
    display: flex;
}
.company-intro-wrap .left-info-wrap .left-img-wrap {
      width: 90px;
      margin-right: 1em;
}
.company-intro-wrap .left-info-wrap .left-img-wrap img {
        width: 100%;
}
.company-intro-wrap .left-info-wrap .right-info-wrap .company-info-name {
      font-size: 18px;
}
.company-intro-wrap .left-info-wrap .right-info-wrap .company-sub-info {
      opacity: 0.8;
      font-size: 12px;
      margin-top: 1em;
}
.company-intro-wrap .left-info-wrap .right-info-wrap .company-infro-wrap {
      border-top-width: 2px;
      border-top-style: dashed;
      border-top-color: #ccc;
}
.company-intro-wrap .left-info-wrap .right-info-wrap .company-infro-wrap .company-infro-major {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
}
.company-intro-wrap .left-info-wrap .right-info-wrap .company-infro-wrap .company-infro-location {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
}
.company-intro-wrap .right-handle-wrap .handle-btns .btn {
    background: #cccccc;
    border-radius: 5px;
    border: transparent;
    padding: 0.5em 0.9em;
    margin-bottom: 0.5em;
    cursor: pointer;
}



