
.higth-search-warp {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 5px;
  border: 1px solid #ccc;
}
.higth-search-warp .search-input {
    height: 2.2em;
    border-radius: 5px;
    outline: none;
    border: 1px solid transparent;
    padding: 0.5em;
}
.higth-search-warp .icons {
    width: 2em;
    height: 2em;
    line-height: 2em;
    cursor: pointer;
}



