
.item-rank-content {
  margin-bottom: .5em;
  cursor: pointer;
}
.item-rank-content .rank-num {
    width: 2em;
    height: 2em;
    line-height: 2em;
    text-align: center;
    display: inline-block;
    border: transparent;
    border-radius: 5px;
    color: white;
    font-size: 20px;
}
.item-rank-content .rank-name {
    display: inline-block;
    padding-left: 1em;
    font-size: 16px;
}



